import { Component, OnInit, ViewChild } from '@angular/core';
import { ActionSheetController, PopoverController, IonInfiniteScroll } from '@ionic/angular';
import { Utils } from 'src/commons/Utils';
import { ErrorMessage, ToastText, MenuText } from 'src/commons/Constants';
import { Activity } from 'src/models/Activity';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { AtividadesEditPage } from '../atividades-edit/atividades-edit.page';
import { Router } from '@angular/router';

//-------------------------------------------------------------------------------------------------------------------------------//

@Component({
  selector: 'app-atividades',
  templateUrl: './atividades.page.html',
  styleUrls: ['./atividades.page.scss'],
})

//-------------------------------------------------------------------------------------------------------------------------------//

export class AtividadesPage {


  @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;


  //-------------------------------------------------------------------------------------------------------------------------------//

  private readonly logHeader = "AtividadesPage>>";

  //-------------------------------------------------------------------------------------------------------------------------------//

  public pilares: Array<string> = new Array<string>();
  public pilar: string;
  private activities: Array<Activity> = new Array<Activity>();
  public activityList: Array<Activity> = new Array<Activity>();
  private searchTerm: string;
  public name: string;
  public value: number;
  private page: number = 1;
  private totalPages: number;

  //-------------------------------------------------------------------------------------------------------------------------------//

  constructor(
    public actionSheetController: ActionSheetController,
    private api: BackendApiProvider,
    private utils: Utils,
    public popoverController: PopoverController,
    private router:Router
    ) {
    const logHeader = this.logHeader + "constructor>>";
    this.getActivities()
    this.pilares = ["Liderança", "SSMA", "MF", "IIM", "MP", "MA", "QM", "WPI"];
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  getActivities() {
    this.api.getActivities(1).then((response: any) => {
      let activities: Array<Activity> = response.list;
      this.totalPages = response.totalPages;
      this.activities = activities;
      this.filter(this.searchTerm);
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  filter(evt) {
    const logHeader = this.logHeader + "filter>>";
    Utils.log(logHeader);
    if (Utils.isStringInvalid(evt)) {
      this.activityList = this.activities;
      return;
    }
    const searchTerm = evt;
    this.searchTerm = searchTerm;
    if (!searchTerm) {
      this.activityList = this.activities;
      return;
    }
    this.activityList = this.activities.filter(n => n.getNome().toLowerCase().includes(searchTerm.toLowerCase()));
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async abrirActionSheet(activity: Activity) {
    const actionSheet = await this.actionSheetController.create({
      header: MenuText.WHAT_DO_YOU_WANT_TO_DO,
      buttons: [
        {
          text: MenuText.EDIT,
          handler: () => {
            this.presentEditPopover(activity);
          }
        },
        {
          text: MenuText.DELETE,
          role: 'destructive',
          handler: () => {
            this.api.removeActivity(activity).then(() => {
              this.activities.splice(this.activities.indexOf(activity), 1);
              this.filter(this.searchTerm)
              this.utils.simpleToast(ToastText.ACTIVITY_REMOVED)
            }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
          }
        },
        {
          text: MenuText.CANCEL, role: 'cancel',
          handler: () => {

          }
        }]
    });
    await actionSheet.present();
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  createActivity() {
    const logHeader = this.logHeader + "createActivity>>";
    if (Utils.isStringInvalid(this.name)) {
      this.utils.simpleToast(ToastText.INSERT_NAME);
      throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>NAME>>" + this.name);
    }
    if (Utils.isStringInvalid(this.pilar)) {
      this.utils.simpleToast(ToastText.INSERT_NAME);
      throw new Error(logHeader + ErrorMessage.INVALID_STRING + ">>PILAR>>" + this.pilar);
    }
    if (Utils.isVarInvalid(this.value)) {
      this.utils.simpleToast(ToastText.INSERT_VALUE);
      throw new Error(logHeader + ErrorMessage.INVALID_VARIABLE + ">>VALUE>>" + this.value);
    }
    if (this.activities.filter(n => n.getNome().toLowerCase() == this.name.toLowerCase()).length > 0) {
      this.utils.simpleToastError(ToastText.ACTIVITY_ALREADY_EXISTS);
      throw new Error(logHeader + ToastText.ACTIVITY_ALREADY_EXISTS + ">>ACTIVITY>>" + new Activity(Number(this.value), this.name, this.pilar));
    }
    this.api.createActivity(new Activity(Number(this.value), this.name, this.pilar)).then((activity: Activity) => {
      this.activityList.push(activity)
      this.filter(this.searchTerm);
      this.utils.simpleToast(ToastText.ACTIVITY_ADDED);
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));

  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  async presentEditPopover(activity: Activity) {

    const modal = await this.popoverController.create({
      component: AtividadesEditPage,
      backdropDismiss: false,
      componentProps: {
        activity: activity,

      }
    });
    await modal.present();
    const { data } = await modal.onDidDismiss();
    if (data) {
      this.activities.splice(this.activities.indexOf(activity), 1);
      this.activities.push(Activity.castFromJSON(data));
      this.filter(this.searchTerm);
    }
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

  loadData(event) {
    this.page++;
    const infiniteScroll = event.target;
    this.api.getActivities(this.page).then((response: any) => {
      let activities: Array<Activity> = response.list;
      this.totalPages = response.totalPages;
      this.activities = this.activities.concat(activities);
      this.filter(this.searchTerm);
      infiniteScroll.complete();
      infiniteScroll.disabled = this.page >= this.totalPages;
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }
  
  //-------------------------------------------------------------------------------------------------------------------------------//

  navigateDashboard() {
    this.router.navigate(['admin', 'dashboard'])
  }

  //-------------------------------------------------------------------------------------------------------------------------------//

}